import React, { useRef } from 'react'
import Grid from '@material-ui/core/Grid'
import { Typography } from '@material-ui/core'
import queryString from 'query-string';

// Client
import FirstStepComponent from './../components/CollectionBreakDownComponent/CollectionBreakDownComponent'
import SecondStepComponent from './../components/CollectionDetailsComponent/CollectionDetailsComponent'

// MEMBER
// import MemberFirstStep from './_members/MemberAnalyzeDetailsComponent/MemberAnalyzeDetailsComponent'
// import MemberSecondStep from './_members/MemberBreakdownComponent/MemberBreakdownComponent'
// import MemberThirdStep from './_members/MemberMeansOfPaymentComponent/MemberMeansOfPaymentComponent'

// Components
import CollectionHeader from './../../../Components/CollectionDistribution/CollectionHeader/CollectionHeader'
import PageFooter from './../../../Components/PageFooter/PageFooter'
import FormStepper from 'SharedComponents/FormStepper/FormStepper'
import { ApproveDialog } from './../components/ActionDialog'

// Store
import { useDispatch, useSelector } from 'react-redux'
import { SetFields } from './../../../Store/actions'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import { useHistory } from 'react-router-dom'

// Styles
import classes from './style.module.scss'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

export default function ValidateCollectionDistributionPage (props) {
  // send data in redux store
  const dispatch = useDispatch()

  // Used to reference element
  const refContainer = useRef()

  // TODO: Change labels and services based in data_type
  const { data_id = null, data_type = '', data_status = '' } = props.match.params
  const history = useHistory()
  const _formData = useSelector(state => state.CollectionForm)
  const { isAdvance } = queryString.parse(props.location.search)
  const [advance, setAdvance] = React.useState(isAdvance === 'true')
  const [taxInfo, setTaxInfo] = React.useState({})

  const [stepper, setStepper] = React.useState({
    showActiveLabel: true,
    stepDetails: ['Collection Breakdown', 'Collection Details'],
    stepNumber: 1
  })

  const [actionDialog, setActionDialog] = React.useState({ clientSubmit: false })
  const [pageData, setPageData] = React.useState({
    cdf: {},
    client: {},
    selectedFees: {}
  })
  const [pageTitle, setPageTitle] = React.useState('')

  React.useEffect(() => {
    // Scrolls the element to top
    refContainer.current.parentElement.scrollTo(0, 0)

    handleGetData()

    switch (data_status) {
      case 'approved':
        setPageTitle(`Approved ${advance ? 'Advance ' : '' }Collection Distribution`)
        BreadcrumbsService.setSelectedItems(['collectionDistributionDashboard', 'listOfCDApproved', 'approvedCD'])
        break
      case 'submitted':
        setPageTitle(`${advance ? 'Advance ' : '' }Collection Distribution Submitted for Approval`)
        BreadcrumbsService.setSelectedItems([
          'collectionDistributionDashboard',
          'listOfCDSubmittedApproval',
          'collectionDistributionSubmittedApproval'
        ])
        break
      case 'disapproved':
        BreadcrumbsService.setSelectedItems([
          'pendingTransactionsList',
          'disapprovedCD'
        ])
        setPageTitle(`Disapproved ${advance ? 'Advance ' : '' }Collection Distribution`)
        break
    }
  }, [])

  // Methods
  const showComponent = componentProp => {
    // TODO: Add Member/Client Components
    if ((data_type && data_type) === 'client') {
      switch (stepper.stepNumber) {
        case 1:
          return <FirstStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
        case 2:
          return <SecondStepComponent {...componentProp} isAdvance={isAdvance} taxInfo={taxInfo} />
        default:
          return ''
      }
    }
  }

  const handleGetData = () => {
    Promise.all([CollectionCDFServices.getCDF({ data_id, isAdvance: advance })])
      .then(res => {
        const [cdfServiceRes] = res
        const { client = {}, cdf_fees = {}, form = {}, ref = {}, tax_type } = cdfServiceRes.data.data

        setPageData(i => ({
          ...i,
          ...cdfServiceRes.data.data,
          reference: ref,
          selectedFees: cdf_fees.cdf_summary || {}
        }))
        dispatch(SetFields({ ...form }))

        CollectionCDFServices.getTaxValue(tax_type).then( res => {
          const { data } = res.data
          setTaxInfo(data[0])
        })
      })
      .catch(err => console.error(`Error Fetching CDF: ${err}`))
  }

  const handleStepChange = stepNumber => {
    setStepper(i => ({ ...i, stepNumber, onStepChanged: stepNumber }))
  }

  const handleCDFApprove = _params => {
    LoadingService.loading(true)
    CollectionCDFServices.approve({ data: { cdf_number: pageData.cdf_number, remarks: _params } })
      .then(res => {
        LoadingService.loading(false)
        history.push('/')
      })
      .catch(e => {
        console.error(`Error Approving CDF: ${e}`)
        LoadingService.loading(false)
      })
  }

  const handleOnSelectSoaTab = params => {
    LoadingService.loading(true)
    if (typeof params === 'string' && params.length <= 0) {
      LoadingService.loading(false)
      return false
    }
    CollectionCDFServices.getCDFFees({
      cdfNumber: pageData.cdf_number,
      soaNumber: params
    })
      .then(res => {
        LoadingService.loading(false)
        const selectedFees = res.data.data.length > 0 ? res.data.data.pop() : {}
        setPageData(i => ({ ...i, selectedFees: selectedFees.cdf_summary || {} }))
      })
      .catch(err => {
        console.error(err)
        LoadingService.loading(false)
      })
  }

  const handleOnCancelClicked = () => {
    history.push(`/`)
  }

  const pageProps = {
    isStepper: true,
    handleStepChange: handleStepChange,
    stepNumber: stepper.stepNumber,
    stepCount: stepper.stepDetails.length,
    isView: true,
    isEdit: false
  }

  return (
    <div className={classes.root} ref={refContainer}>
      <Grid
        container
        className={`${classes['grid-container']} ${classes['bg-white']} ${classes.header}`}
        spacing={2}
        direction='column'
        justify='space-between'
      >
        <Grid item xs={12}>
          <Grid container>
            <Grid item className={classes['grid-item']}>
              <Typography id='collection-distribution-page-header' variant='h5'>
                {pageTitle}
              </Typography>
            </Grid>
            <Grid item className={classes['grid-item']}>
              <FormStepper {...stepper} />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              <CollectionHeader {...pageData} disabled />
            </Grid>
          </Grid>
          <Grid container>
            <Grid item xs={12}>
              {showComponent({
                soa: pageData.cdf_soa,
                disabled: true,
                handleOnSelectSoaTab,
                totalFees: pageData.cdf_fees?.cdf_summary || {},
                selectedFees: pageData.selectedFees
              })}
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <PageFooter
            {...pageProps}
            handleOnCloseClicked={() => {
              history.push('/')
            }}
            handleOnCancelClicked={() => handleOnCancelClicked()}
            handOnEditClicked={() => history.push(`/collection/collection-distribution/client/${pageData.cdf_number}`)}
          />
        </Grid>
      </Grid>
    </div>
  )
}
