/* eslint-disable no-unused-vars */
import React from 'react'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import FormHelperText from '@material-ui/core/FormHelperText';
import InputLabel from '@material-ui/core/InputLabel';
import MuiSelect, { SelectProps } from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

Select.propTypes = {
  defaultItemText: PropTypes.string.isRequired,
  data: PropTypes.arrayOf(PropTypes.shape({
    value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    text: PropTypes.string.isRequired,
  })),
  error: PropTypes.string
}

/**
 * @param {SelectProps} ...props 
 */
export default function Select({ defaultItemText, disableDefaultItemText = false, data = [], error = '', ...props }) {
  const classes = useStyles();

  return (
    <FormControl variant="outlined" error={error.length > 0} className={classes.formControl}>
      {props.label && (
        <InputLabel id={`${props.id}-label`}>{props.label}</InputLabel>
      )}

      <MuiSelect labelId={`${props.id}-label`} {...props}>
        <MenuItem value="" disabled={disableDefaultItemText}>
          <em>{defaultItemText}</em>
        </MenuItem>

        {data.map(item => (
          <MenuItem value={item.value}>{item.text}</MenuItem>
        ))}
      </MuiSelect>

      {error.length > 0 && (
        <FormHelperText>{error}</FormHelperText>
      )}
    </FormControl>
  )
}

const useStyles = makeStyles((theme) => ({
  formControl: {
    width: '100%',
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));