import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import PageFooter from './../../../Components/PageFooter/PageFooter'
import Checkbox from './../../../Components/FormComponents/Checkbox/Checkbox'
import SearchTextComponent from './../../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import { CustomTable as Table } from './../../../Components/CustomTable'

// Services
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import LetterTemplateServices from 'Services/Api/CollectionMaintenance/LetterTemplateServices';

// Utilities
import pick from 'lodash/pick'
import upperFirst from 'lodash/upperFirst'
import truncate from 'lodash/truncate'
import moment from 'moment'

// Styles
import classes from './LetterTemplate.module.scss'

export default function LetterTemplate() {
  const history = useHistory()
  const [data, setData] = React.useState({
    header: HEADERS,
    body: [],
    count: 0,
    limit: 10,
    order: true,
    orderBy: 'date_modified',
    page: 1,
    sort: '',
    search: ''
  })

  const handleSearchChanged = search => {
    fetchData({ search })
  }

  const fetchData = (params = {}) => {
    LoadingService.loading(true)

    LetterTemplateServices.index({
      ...pick(data, ['limit', 'page', 'range', 'rangeTarget', 'filter']),
      sort: `${data.orderBy}|${data.order ? 'desc' : 'asc'}`,
      ...params
    })
      .then(res => {
        const { count, limit, page, results } = res.data.data
        setData(prevState => ({
          ...prevState,
          count,
          body: results,
          limit,
          page,
        }))
        LoadingService.loading(false)
      })
      .catch(err => {
        console.log(`Error fetching letter template list: ${err}`)
        LoadingService.loading(false)
      })
  }

  React.useEffect(() => {
    fetchData()
  }, [])

  return (
    <>
      <Grid item xs={12}>
        <Grid container className={classes.search}>
          <Grid item xs>
            <SearchTextComponent
              placeholder='Search from the list of letter templates'
              displaySettings={false}
              value={data.search}
              onChange={handleSearchChanged}
            />
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={12}>
        <Grid container>
          <Grid item xs>
            <Table
              hasLink
              data={data}
              onDataListChange={fetchData}
              setData={setData}
              showTextField
              body={template =>
                template.map((template, index) => (
                  <TableRow key={`template-letter-table-row-${index}`}>
                    <TableCell id={`template-letter-table-cell-letter-type-${index}`}>
                      {upperFirst(template.letter_type)}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-template-name-${index}`}>
                      {template.name}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-entity-type-${index}`}>
                      {upperFirst(template.entity_type)}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-description-${index}`}>
                      {truncate(template.description)}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-author-${index}`}>
                      {`${template.author ? template.author?.first_name + ' ' + template.author?.last_name : ''}`}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-last-updated-${index}`}>
                      {moment(template.date?.modified).format('ll')}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-published-${index}`}>
                      <Checkbox checked={template.date?.published} />
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-action-${index}`}>
                      <Link to={`/`}>
                        <Typography
                          id={`template-letter-view-${index}`}
                          variant='caption'
                          className='text-link underline'
                        >
                          Generate
                        </Typography>
                      </Link>
                    </TableCell>
                  </TableRow>
                ))
              }
            />
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={12}>
        <PageFooter
          showCollectionMaintenanceBtn
          handleCreateLetterTemplate={() => history.push('/collection/maintenance/create-letter-template')}
        />
      </Grid>
    </>
  )
}

const HEADERS = [
  {
    id: 'as-letter-type',
    numeric: false,
    disablePadding: false,
    name: 'Letter Type',
    sortField: 'letter_type',
    match: 'any'
  },
  {
    id: 'as-name',
    numeric: false,
    disablePadding: false,
    name: 'Template Name',
    sortField: 'name',
    match: 'any'
  },
  {
    id: 'as-entity-type',
    numeric: false,
    disablePadding: false,
    name: 'Category',
    sortField: 'entity_type',
    match: 'any'
  },
  {
    id: 'as-description',
    numeric: false,
    disablePadding: false,
    name: 'Description',
    sortField: 'description',
    match: 'any'
  },
  {
    id: 'as-author',
    numeric: false,
    disablePadding: false,
    name: 'Author',
    sortField: 'author',
    match: 'any'
  },
  {
    id: 'as-last-update',
    numeric: false,
    disablePadding: false,
    name: 'Last Update',
    sortField: 'date_modified',
    match: 'any'
  },
  {
    id: 'as-published',
    numeric: false,
    disablePadding: false,
    name: 'Published',
    sortField: 'published',
    match: 'any'
  },
  {}
]
